@charset "utf-8";

.container {
  width: min(100% - 30px, 1080px);
  margin-inline: auto;
}

.flex {
  display: flex;
}

.space-between {
  justify-content: space-between;
}

section {
  padding: 60px 0;
}


/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Header                                               */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
header {
  height: 60px;
  background-color: rgb(92 23 141);
}

h1, .header-list a {
  color: #fff;
}

h1 {
  font-size: 2rem;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 60px;
  letter-spacing: 0.2px;
}

.header-list li {
  cursor: pointer;
  margin-right: 22px;
  line-height: 60px;
  font-size: 18px;
}

.header-list a {
  text-decoration: none;
}


/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Gallery                                              */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
.purple {
  color: rgb(99, 25, 152);
}

.gallery h2, .gallery p {
  text-align: center;
}

.gallery h2 {
  font-size: 2.8rem;
  font-weight: 600;
  color: rgb(36 32 45);
}

.gallery p {
  font-size: 16px;
  font-weight: 400;
  color: rgb(81 76 91);
}

.movie-list {
  margin: 40px 0;
}

.movie-list img {
  width: 90%;
  border-radius: 5px;
  box-shadow: 1px 1px 4px rgb(189 187 193 / 0.45);
  transition: all 0.25s ease-in-out;
  cursor: pointer;
}

.movie-list img:hover {
  transform: translateY(-4px);
}

.movie-list p {
  padding-top: 8px;
  font-size: 18px;
  font-weight: 500;
  color: rgb(36 32 45);
}
